@charset "UTF-8";

/**
 * Основные стили
 **/

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0; }

html {
	font-size: 16px;
	color: $black; }

body {
	font-family: 'Roboto', 'Arial', sans-serif;
	overflow-wrap: break-word;
	word-wrap: break-word; }

.container {
	@include min-max(320px, 100%, 0); }

.wrap {
	@include min-max(320px, 1140px, 15px); }
