/**
 * Основные стили
 **/
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

html {
  font-size: 16px;
  color: #000; }

body {
  font-family: 'Roboto', 'Arial', sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word; }

.container {
  min-width: 320px;
  max-width: 100%;
  padding: 0 0;
  box-sizing: border-box;
  margin: 0 auto; }

.wrap {
  min-width: 320px;
  max-width: 1140px;
  padding: 0 15px;
  box-sizing: border-box;
  margin: 0 auto; }

.fixed {
  position: fixed;
  top: 0;
  left: 50%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
  transform: translateX(-50%);
  z-index: 10; }
  .fixed__wrap {
    display: flex;
    align-items: center; }
  .fixed__hamburger {
    margin-right: 11.3%;
    flex-basis: 24px;
    flex-shrink: 0;
    cursor: pointer; }
    .fixed__hamburger span {
      display: block;
      width: 100%;
      height: 2px;
      margin-bottom: 4px;
      background-color: #FCDB50;
      border-radius: 4px; }
      .fixed__hamburger span:nth-child(3) {
        width: 75%;
        margin-left: auto;
        margin-bottom: 0; }
  .fixed__repair, .fixed__map {
    padding-left: 48px;
    background: url("../img/repair.svg") left no-repeat;
    padding: 24px 0 24px 48px; }
    .fixed__repair p, .fixed__map p {
      font-size: 1.125rem;
      line-height: 1.3125rem; }
    .fixed__repair span, .fixed__map span {
      display: block;
      font-size: 0.875rem;
      line-height: 1rem; }
  .fixed__phone {
    margin-left: auto; }
    .fixed__phone a {
      display: block;
      text-decoration: none; }
    .fixed__phone span {
      display: none;
      color: #fff;
      font-size: 1.5rem;
      line-height: 1.75rem; }
  .fixed__map {
    display: none;
    background-image: url("../img/map.png"); }
  .fixed__button {
    display: block;
    padding-top: 14px;
    padding-bottom: 14px;
    width: 100%;
    background-color: #FCDB50;
    color: #2f2f2f;
    font-size: 0.875rem;
    line-height: 1rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color .5s;
    display: none;
    margin-left: 24px;
    max-width: 164px; }
    .fixed__button:hover {
      background-color: #FFE67C; }
  .fixed__callback {
    display: none;
    font-size: 0.875rem;
    line-height: 1rem;
    margin-left: 24px; }

@media (min-width: 480px) {
  .fixed__hamburger, .fixed__repair {
    margin-right: 5%; }
  .fixed__map {
    display: block; } }

@media (min-width: 680px) {
  .fixed__phone img {
    display: none; }
  .fixed__phone span {
    display: block; } }

@media (min-width: 880px) {
  .fixed__button {
    display: block; } }

@media (min-width: 1024px) {
  .fixed__callback {
    display: block; }
  .fixed__hamburger {
    position: relative; }
    .fixed__hamburger::after {
      position: absolute;
      top: 50%;
      right: -45px;
      content: 'Меню';
      font-size: 0.875rem;
      line-height: 1rem;
      transform: translateY(-50%); } }
